// src/pages/About.js
import React from 'react';
import { Container, Typography, Box } from '@mui/material';

const About = () => {
  return (
    <Container maxWidth="lg" style={{ padding: '2rem' }}>
      <Box>
        <Typography variant="h4" gutterBottom>
          About AIQ
        </Typography>
        <Typography variant="body1">
          AIQ is designed to revolutionize the interview process using cutting-edge AI technology. Our mission is to help candidates feel more prepared and confident by simulating interviews based on personalized information, including resumes and job descriptions. The AI-driven platform also provides feedback and tips for improvement, giving users a competitive edge in their job search.
        </Typography>
      </Box>
    </Container>
  );
};

export default About;
