// src/pages/Home.js
import React from 'react';
import { Container, Typography, Box } from '@mui/material';

const Home = () => {
  return (
    <Container maxWidth="lg" style={{ padding: '2rem' }}>
      <Box>
        <Typography variant="h4" gutterBottom>
          Welcome to AIQ - Your AI Interview Assistant
        </Typography>
        <Typography variant="body1">
          An innovative AI-powered interviewer designed to simulate real interview experiences. 
          It helps candidates prepare by generating personalized questions based on their resumes and job descriptions.
        </Typography>
      </Box>
    </Container>
  );
};

export default Home;
