// Dashboard.js
import React from 'react';
import { useNavigate } from 'react-router-dom';

const Dashboard = () => {
  const navigate = useNavigate();

  // Subjects for the dashboard
  const subjects = [
    { id: 1, name: 'DBMS' },
    { id: 2, name: 'Python' },
    { id: 3, name: 'Java' },
    { id: 4, name: 'MERN Stack' },
    { id: 5, name: 'AWS' },
    { id: 6, name: 'C' }
  ];


  const handleNavigate = (subject) => {
    navigate(`/interviewOn/${subject.toLowerCase()}`);
  };

  return (
    <div style={{ padding: '20px', textAlign: 'center' }}>
      <h1>Dashboard</h1>
      <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', gap: '20px' }}>
        {subjects.map((subject) => (
          <div key={subject.id} style={{ border: '1px solid #ccc', padding: '10px', borderRadius: '8px', width: '150px' }}>
            <h3>{subject.name}</h3>
            <button onClick={() => handleNavigate(subject.name)}>Take Test !</button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Dashboard;
